import * as THREE from "three";
import React, { Component } from "react";
import Footer from "../Footer/Footer";
import ProjectPage from "../Page/ProjectPage";
import BP from "../Page/BioPage";
import ContactPage from "../Page/ContactPage";


class RenderWindow extends Component {
  state = { };
  constructor(props) {
    super(props);
  }

  componentDidMount() { 
    var loader = new THREE.FontLoader();
    var self = this;
loader.load( process.env.PUBLIC_URL+'/Ubuntu_Thin_Regular.json', function ( font ) {
  var scene = new THREE.Scene();
  var camera = new THREE.PerspectiveCamera( 75, window.innerWidth/window.innerHeight, 0.1, 1000 );
  var renderer = new THREE.WebGLRenderer({alpha:true});
  renderer.setSize(window.innerWidth, window.innerHeight);
  camera.position.z = 160;
  var textGeometry = new THREE.TextGeometry( "Zachary Hills, \n Full Stack Developer", {
    font: font,
    size: 8,
    height: 10,
    curveSegments: 12,
    bevelThickness: 1,
    bevelSize: 1,
    bevelEnabled: true

  });
  var dirLight = new THREE.DirectionalLight();
  dirLight.position.set(25, 23, 15);
  scene.add(dirLight);


  var dirLight2 = new THREE.DirectionalLight();
  dirLight2.position.set(-25, 23, 15);
  scene.add(dirLight2);

  var material = new THREE.MeshPhongMaterial( { color: 0x00ff00, specular:0x000000 } );
  var mesh = new THREE.Mesh(textGeometry, material);
  for ( var i = 0; i < mesh.geometry.vertices.length; i ++ ) {
      for( var j = 0; j < 3; j++ ) {
          let color = new THREE.Color( 0xffffff );
          color.setHex( Math.random() * 0xffffff );
        mesh.geometry.colors[i + j] = color;
      }
  }
  scene.add(mesh);
  mesh.geometry.colorsNeedUpdate = true;
  /**
   * 
   * @param {THREE.Geometry} geometry 
   */
  var explode = function (geometry) {
    for (var i = 0, il = geometry.vertices.length; i < il; i++) {
      geometry.vertices[i].z += 1;
    }
    geometry.verticesNeedUpdate = true;
  };
  var animate = function () {
    requestAnimationFrame(animate);
    explode(mesh.geometry);
    renderer.render(scene, camera);
  };
  self.mount.appendChild(renderer.domElement);
  animate();
});   

  }

  render() {
    return (
      <div id="renderwindow">
        <div
          style={{
            height: window.innerHeight - 100,
            backgroundImage: `url("${process.env.PUBLIC_URL}/codebackground.png")`
          }}
          ref={ref => (this.mount = ref)}
        ></div>
        <Footer />
        <BP />
        <ProjectPage cardWidth="400px" cardHeight="400px" />
        <ContactPage />
      </div>
    );
  }
}

export default RenderWindow;
